<!--  -->
<template>
    <div class="container" @click="handleGallaryClick">
        <div class="wrapper">
            <swiper class='swiper' :options="swiperOption">
                <swiper-slide v-for="(item,index) of imgs" :key="index">
                    <img class="gallary-img" :src="item">
                </swiper-slide>
                <div class="swiper-pagination"  slot="pagination"></div>
            </swiper>
        </div>
    </div>
</template>

<script>
export default {
    name:'CommonGallary',
    props:{
        imgs:{
            type:Array,
            default(){
                return []
            }
        }
    },
    data() {
        return {
            swiperOption: {
            //  swiper  api 
                pagination:{
                    el:'.swiper-pagination',
                    type: 'fraction',
                    observeParents:true, //domwiper
                    observe:true         //domwiper
                },
                loop:true
            }
        }
    },
    methods:{
        handleGallaryClick(){
            this.$emit('close')
        }
    }

}
</script>

