<!--  -->
<template>
    <div >
        <div class="search">  
            <input v-model="keyword" type="text" class="search-input" placeholder=""> 
        </div>
        <div class="search-content" ref="wrapper" v-show="keyword"> 
            <ul class="item-list">
                <li class="item border-bottom" v-for="item of list" :key="item.id">
                    {{item.name}}
                </li>
                <li class="item border-bottom" v-show="hasNoData"></li>
            </ul>
        </div>
    </div> 
</template>

<script>
    import BScroll from 'better-scroll'

    export default {
        name:'CitySearch',
        props:{
            cities:Object
        },
        data () {
            return {
                keyword:'',
                list:[],
                timer:null,
            }
        },
        computed:{
            hasNoData(){
                return !this.list.length
            }
        },
        mounted(){
            this.scroll= new BScroll(this.$refs.wrapper)
        },
        watch:{
            keyword(){
                if(this.timer){
                    clearTimeout(this.timer)
                }
                this.timer = setTimeout(()=>{
                    const result = []
                    for(let i in this.cities){
                        this.cities[i].forEach((value) => {
                            if(value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1 ){
                                result.push(value)
                            }
                        });
                    }
                    this.list = result
                },100)
            } 
        }

}

</script>

<style lang='stylus' scoped>
    @import '~@/assets/styles/varibles.styl'
    
    .search
        background-color $bgColor
        height .72rem
        padding 0 .1rem
        .search-input
            width 100%
            text-align center
            height .64rem
            border-radius .1rem
            color #666
            padding 0 .1rem
            box-sizing border-box
    .search-content
        z-index 1
        position absolute
        top 1.58rem
        left 0
        right 0
        bottom 0
        overflow hidden
        background-color #fff
        .item-list
            .item
                line-height .8rem
                text-align center right 
                padding-left .2rem
        
</style>




