<!--  -->
<template>
    <div >
        <home-heder></home-heder>
        <home-swiper :list="swiperList"></home-swiper>
        <home-icon :list="iconList"></home-icon>
        <home-recommend :list="recommendList"></home-recommend>
        <home-weekend :list="weekendList"></home-weekend>
        <p>{{text}}</p>
    </div>
</template>

<script>
import HomeHeder from './components/Header.vue'
import HomeSwiper from './components/Swiper.vue'
import HomeIcon from './components/Icon.vue'
import HomeRecommend from './components/Recommed.vue'
import HomeWeekend from './components/Weekend.vue'
import axios from 'axios'
import {mapState} from 'vuex'
export default {
    name:'Home',
    data(){
        return{
            lastCity:'',
            swiperList:[],
            iconList:[],
            recommendList:[],
            weekendList:[], 
            text:"sdfsdfj",
        }
    },
    computed:{
        ...mapState(['city'])
    },
    components:{
        HomeHeder,
        HomeSwiper,
        HomeIcon,
        HomeRecommend,
        HomeWeekend

    },
    methods:{
        getHomeInfo(){
            axios.get('https://lg6613180.github.io/mywork/mock/mock.html')
                .then(this.getHomeInfoSucc)
        },
        getHomeInfoSucc(res){
            res=require("./index.json")//json
            //this.text=res
            //res=res.data.data
            if(res.ret&&res.data){
                this.swiperList=res.data.swiperList
                this.iconList=res.data.iconList
                this.recommendList=res.data.recommendList
                this.weekendList=res.data.weekendList
            }
        }
    },
    mounted(){
        this.getHomeInfo()
        this.lastCity=this.city
    },
    activated(){
        if(this.lastCity !== this.city){
            this.lastCity=this.city
            this.getHomeInfo()
        }
        
    }
}

</script>
