<!--  -->
<template>
    <div class="list" ref="wrapper"> 
        <div>
            <div class="area">
                <div class="title"></div>
                <div class="button-list">
                    <div class="button">{{city}}</div>
                </div>
            </div>
            <div class="area">
                <div class="title"></div>
                <div class="button-list">
                    <div class="button" v-for="item of hotCities" :key="item.id" @click="handleCityClick(item.name)">{{item.name}}</div>
                </div>
            </div>
            <div class="area" v-for="(item,key) of cities" :key="key" :ref="key">
                <div class="title">{{key}}</div>
                <div class="item-list">
                    <div class="item border-bottom" v-for="inneritem of item" :key="inneritem.id" @click="handleCityClick(inneritem.name)">
                        {{inneritem.name}}
                    </div>
                </div>
            </div>
        </div>
    </div> 
</template>

<script>
    import BScroll from 'better-scroll'
    import {mapState,mapMutations} from 'vuex'

    export default {
        name:'CityList',
        props:{
            hotCities : Array,
            cities : Object,
            letter : String
        },
        data () {
            return {}
        },
        computed:{
            ...mapState(['city'])
        },
        methods:{
            handleCityClick(city){
                this.changeCity(city)
                this.$router.push('/')
            },
            ...mapMutations(['changeCity'])
        },
        mounted(){
            this.scroll= new BScroll(this.$refs.wrapper, {click: true})
        },
        watch:{
            letter(){
                if (this.letter){
                    const element = this.$refs[this.letter][0]
                    this.scroll.scrollToElement(element)
                }
            }
        }
    }

</script>

